<template>
    <b-card v-if="author" @click="postCardOnClick">
        <b-media>
            <template #aside>
              <b-img blank blank-color="#ccc" width="64" alt="placeholder"></b-img>
            </template>

            <div class="d-inline-flex">
                <h6 class="mt-0">{{ author.Name }}</h6>
                <div>{{ post.Timestamp }}</div>
            </div>
            
            <p class="mb-0">
              {{ post.Text }}
            </p>
        </b-media>
    </b-card>
</template>

<script>
    import axios from 'axios';

    export default {
        name: 'PostCard',
        props: ["postId", "userId", "jwtToken"],
        data() {
            return {
                getPostUrl: "https://localhost:6868/Posts/postId",
                getUserProfileUrl: "https://localhost:6868/Users/userId/profile",
                post: {},
                author: null,
            }
        },
        async mounted() {
            await this.getPost();
            await this.getAuthorProfile();
        },
        methods: {
            async getPost() {
                await axios.get(
                    this.getPostUrl.replace("postId", this.postId),
                    {
                        headers: {
                            Authorization: `Bearer ${this.jwtToken}`
                        }
                    }
                ).then((res) => {
                    this.post = res.data;
                    //console.log(this.post);
                }).catch((res) => {
                    console.log(res);
                });
            },

            async getAuthorProfile() {
                await axios.get(
                    this.getUserProfileUrl.replace("userId", this.post.AuthorId),
                    {
                        headers: {
                            Authorization: `Bearer ${this.jwtToken}`
                        }
                    }
                ).then((res) => {
                    this.author = res.data;
                    //console.log(this.author);
                }).catch((res) => {
                    console.log(res);
                });
            },

            postCardOnClick() {
                if (this.$route.path !== `/post/${this.postId}`) {
                    this.$router.push({
                        name: 'post',
                        params: {
                            postId: this.postId,
                            userId: this.userId,
                            jwtToken: this.jwtToken
                        }
                    })
                }
            },
        },
        watch: {
            post: {
                immediate: true,
                deep: true,
                handler: function () {
                    //console.log(this.post);
                }
            },
        }
    }
</script>

